/* Navbar content */
*{
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  text-decoration: none;
}
.content{
  min-height: 70vh;
}
.navbar{
  background-color: #f47a60;
  padding: 20px;
  display: flex;
  align-items: center;
}
.navbar h1{
  color: #ffffff;
  display: flex;
}
.navbar .links{
  margin-left: auto;
}
.navbar a{
  color: #ffffff;
  font-size: 20px;
  padding: 20px;
  color: #ffffff;
  text-decoration: none;
  margin-left: 10px;
}
.navbar a:hover{
  background-color: #ffffff;
  padding: 45px;
  color: #316879;
  text-decoration: none;
  margin-left: 10px;
}

/* main blog  */

.main{
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.main h1{
  color: #f47a60;
}
.blog-list{
  padding: 60px;
  text-align: center;
  width: 60%;
  border-bottom: 8px solid #316879;
  border-radius: 10px;
  background-color: rgb(241, 244, 247);
  margin-bottom: 20px;
  text-decoration: none;
}
.blog-list .title{
  font-size: 20px;
  color: #316879;
  font-weight: bold;
  text-decoration: none;
}
.blog-list .author{
  color: #f47a60;
  margin-top: 10px;
}
.blog-list:hover{
  border-bottom: 8px solid #f47a60;
}

/* footer content */
footer{
  background-color: #316879;
  padding: 50px;
  color: #ffffff;
  text-align: center;
  text-decoration: none;
}

/* Contact Page */

.contact{
  display: flex;
  flex-direction: column;
  align-items: center;
}
.contact h1{
  color: #f47a60;
}
.contact form{
  width: 70%;
  display: flex;
  flex-direction: column;
  margin: 30px;
}
.contact input {
  margin: 10px;
  padding: 20px;
  width: 60%;
  border: 2px solid #316879;
  border-bottom: 6px solid #316879;
  border-radius: 10px;
}
.contact input:hover {
  margin: 10px;
  padding: 20px;
  border: 2px solid #f47a60;
  border-bottom: 6px solid #f47a60;
  border-radius: 10px;
}

.contact textarea{
  margin: 10px;
  padding: 20px;
  width: 60%;
  border: 2px solid #316879;
  border-bottom: 6px solid #316879;
  border-radius: 10px;
}
.contact textarea:hover{
  margin: 10px;
  padding: 20px;
  border: 2px solid #f47a60;
  border-bottom: 6px solid #f47a60;
  border-radius: 10px;
}
.contact button{
  background-color: #316879;
  color: #ffffff;
  width: 200px;
  margin: 10px;
  padding: 15px;
  border: 2px solid #316879;
  border-bottom: 6px solid #ced7d8;
  border-radius: 10px;
}
.contact button:hover{
  background-color: #f47a60;
  color: #ffffff;
  margin: 10px;
  padding: 15px;
  border: 2px solid #f47a60;
  border-bottom: 6px solid #316879;
  border-radius: 10px;
}

/* Success Page */

.success {
  display: flex;
  flex-direction: column;
  align-items: center;

}
.success h1{
  color: #316879;
}
.success a{
  text-decoration: none;
  color: #f47a60;
}

/* Article page */

article{
  width: 60%;
  margin: auto;
}
article h2{
  background-color: #316879;
  color: #ffffff;
  padding: 30px;
  text-align: center;
  border-radius: 0  0 30px 30px ;
}
article div{
  background-color: #f47a60;
  color: #ffffff;
  width: 190px;
  padding: 15px;
  border-radius: 0 30px 30px 0 ;
}
article p{
  background-color: #ced7d8;
  padding: 20px;
  font-size: 18px;
}